import React from 'react';
import Container from './Container';
import Row from './Row';
import Column from './Column';
import EmblemFaction from '../assets/emblemFaction.jpg';

function BandPromo() {
    return (
        <div>
            <Container>
                <Row>
                    <Column size="12">
                        <div className="bandPromoBox">
                            <Row>
                                <Column size="xl-6">
                                <div className="card bg-dark text-white">
                                        <img src={EmblemFaction} alt="The Emblem Faction logo"/>
                                    </div>
                                </Column>
                                <Column size="xl-6">
                                    <div className="card bg-dark text-white">
                                        <iframe id="embededVideo" src="https://www.youtube.com/embed/Zc28dPM01Mw" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                                    </div>
                                </Column>
                            </Row>
                        </div>
                    </Column>
                </Row>
            </Container>
        </div>
    )
}

export default BandPromo;